/*
 *
 * charleston
 *
 */
@import 'shared';

// mixin to extend charleston filter
// @mixin charleston
// @param $filters... {filter} - Zero to many css filters to be added
// @example
//   img {
//     @include charleston;
//   }
//   or
//   img {
//     @include charleston(blur(2px));
//   }
//   or
//   img {
//     @include charleston(blur(2px)) {
//       /*...*/
//     };
//   }
@mixin charleston($filters...) {
  @include filter-base;
  filter: contrast(.8) brightness(1.1) blur(2px) $filters;

  img {
    filter:grayscale(1);
    opacity: .4;
  }

  &::after {
    background: linear-gradient(45deg, rgba(#91c6e9,1), rgba(#73b6e2,.5));
    mix-blend-mode: overlay;
  }

  @content;
}

// charleston Instagram filter
%charleston,
.charleston {
  @include charleston;
}
